export function createOrder(data) {
	//jsonlist
	var _data = data.A_orderVos.reduce((acc, cur) => {
		acc.push({
			businessCode: cur.businessCode,
			loginAccount: JSON.parse(sessionStorage.getItem("loginAccount")),
			shopNum: cur.shopNum,
			skuCode: cur.skuCode,
		});
		return acc;
	}, []);
	console.log("");
	console.log(_data);
	//debugger
	return request({
		url: siteName + "/order/addOrder",
		method: "post",
		data: _data,
	});
}

