<template>
	<div class="slider" ref="slider">
		<div class="slider-group" ref="sliderGroup">
			<slot></slot>
		</div>
		<div class="dots">
			<span
				class="dot"
				:class="{ current: currentIndex == index }"
				:key="index"
				v-for="(dot, index) in dotes"
			></span>
		</div>
	</div>
</template>

<script>
import BScroll from "better-scroll";
//dom
function addClass(el, className) {
	//
	var A_newClass = el.className.split(" ");
	A_newClass.push(className);
	el.className = A_newClass.join(" ");
}
export default {
	name: "AppSwipper",
	data() {
		return {
			dotes: [],
			//
			currentIndex: 0,
		};
	},
	props: {
		//
		autoPlay: {
			type: Boolean,
			default: true,
		},
		//
		loop: {
			type: Boolean,
			default: true,
		},
		//
		intervalTime: {
			type: Number,
			default: 3000,
		},
	},
	watch: {
		bannerList() {
			setTimeout(() => {
				// 
				this._setWidth();
				// 
				this._initDots();

				// 
				this._initSwipper();
				// 
				if (this.autoPlay) {
					this.play();
				}
			}, 20);
		},
	},
	mounted() {
		/**
     *  dom
      dom
     * 1
     * 2v-ifundefined
     * 3watch
     */
		setTimeout(() => {
			//
			this._setWidth();
			//
			this._initSwipper();
			//
			this._initDots();
			//
			if (this.autoPlay) {
				this.play();
			}
		}, 200);
		console.log();

		//
		window.addEventListener("resize", () => {
			if (!this.slider) {
				return;
			}
			this._setWidth(true);
			//TODO:
			this.slider.refresh();
		});
	},
	methods: {
		_setWidth(reset) {
			var Dom_children = this.$refs.sliderGroup.children;
			var width = 0;
			//banner
			var sliderWidth = this.$refs.slider.clientWidth;
			//
			for (var i = 0; i < Dom_children.length; i++) {
				var child = Dom_children[i];
				addClass(child, "slider-item");
				//
				child.style.width = sliderWidth + "px";
				//
				width += sliderWidth;
			}
			//
			//
			if (this.loop && !reset) {
				width += 2 * sliderWidth;
			}
			//
			this.$refs.sliderGroup.style.width = width + "px";
		},
		//
		_initSwipper() {
			this.slider = new BScroll(this.$refs.slider, {
				scrollY: false,
				scrollX: true,
				momentu: false,
				// click: true,
				snap: {
					loop: this.loop,
					threshold: 0.3,
					speed: 400,
				},
			});

			//
			this.slider.on("scrollEnd", () => {
				//
				var pageIndex = this.slider.getCurrentPage().pageX;
				//,
				if (this.loop) {
					this.pageIndex -= 1;
				}
				this.currentIndex = pageIndex;

				if (this.autoPlay) {
					clearTimeout(this.timer);
					this.play();
				}
			});
		},
		//
		play() {
			this.timer = setTimeout(() => {
				this.slider.next(400);
			}, this.intervalTime);
		},

		//
		_initDots() {
			//
			this.dotes = new Array(this.$refs.sliderGroup.children.length);
		},
	},
};
</script>

<style lang="scss" scoped>
.slider {
	position: relative;
	min-height: 1px;
	overflow: hidden;
	.slider-group {
		position: relative;
		overflow: hidden;
		white-space: nowrap;
		.slider-item {
			float: left;
			overflow: hidden;
			text-align: center;
			box-sizing: border-box;
			img {
				display: block;
				width: auto;
				height: auto;
				max-width: 100%;
				max-height: 100%;
			}
		}
	}
	.dots {
		position: absolute;
		right: 0;
		left: 0;
		bottom: 0.5rem;
		text-align: center;
		font-size: 0;
		.dot {
			display: inline-block;
			margin: 0 0.25rem;
			width: 0.5rem;
			height: 0.5rem;
			border-radius: 50%;
			background: rgba($color: #000000, $alpha: 0.3);
			&.current {
				background: chocolate;
				width: 1rem;
				border-radius: 4px;
			}
		}
	}
}
</style>
